import { Component } from '@angular/core';
import { ViewController, NavController ,AlertController } from 'ionic-angular';
import { TeacherList  } from '../teacherList/teacherList';
import { ChangeSchool  } from '../changeSchool/changeSchool';

@Component({
  templateUrl: 'teacherSchoolstoryPopovermenu.html'
   
})

export class SchoolStoryPopovermenuPage {

teacherList =TeacherList;

constructor(public viewCtrl: ViewController,public navCtrl: NavController,public alertCtrl: AlertController) {}


close() {
    this.viewCtrl.dismiss();
  }


changeSchool(){
  this.navCtrl.push(ChangeSchool);
  
	
}


teacherlist(){
  
  this.navCtrl.push(TeacherList);
}



}




