import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import * as config  from '../assets/json/config';
import * as func from '../app/function';

@Injectable()
export class ProfileService {
 constructor(private http: Http) { }
 


 editProfile(data,memberno){
        return new Promise((resolve, reject) => {
        this.http.post(func.global_function.getToken()+':'+config.data.port+'/student/update',{
         token:config.data.api_token,         
          member_no:memberno,
          name:data.name
          
         })
          .map(res => res.json())
          .subscribe(res => {
            resolve(res);
          }, (err) => {
            reject(err);
          });
    });

   }
      
    deleteAccount(type,memberno){
        return new Promise((resolve, reject) => {
        this.http.post(func.global_function.getToken()+':'+config.data.port+'/'+type+'/delete',
        {
         token:config.data.api_token,         
          member_no:memberno
         })
          .map(res => res.json())
          .subscribe(res => {
            resolve(res);
          }, (err) => {
            reject(err);
          });
    });

   }
       
  }
 
 


